'use strict';

import Vue from 'vue';
import Vuex from 'vuex';
import { isSupportPromise } from './type/utils/index.js';
// 为 Vuex 准备 Promise 适配 U3,iOS78
if ( typeof window !== 'undefined' ) {
  if ( !isSupportPromise(window.Promise) ) {
    const localPromise = require('babel-runtime/core-js/promise');
    window.Promise = localPromise.default || localPromise;
  }
}

// 不在browser环境 或
// 在browser环境但没有window.Vue
if ( typeof window === 'undefined' || !window.Vue ) {
  Vue.use(Vuex);
}


import { Page } from './type/page.js';
import { Component, ObjectFunc } from './type/component/index.js';
import { Property, Watch } from './type/property/index.js';

export function use (plugin) {
  return Vue.use(plugin);
}
export const Store = Vuex.Store;

// 用于自动注入装饰器时，不需要显式引入 Property 这个变量
Component.Property = Property;
Component.Object = ObjectFunc;

export {
  Page,
  Component,
  Property,
  Watch
}

// 开始加载插件
import { loadPlugin } from './type/plugins.js';

try {
  loadPlugin({
    Vue,
    use,
    Page,
    Component,
    Property,
    ObjectFunc,
    Watch,
    Store,
    Vuex
  }, 
  // easy to inject other name such as log, util, etc.
  module.exports)
} catch(ex) {
  console.error(ex)
  throw ex;
}